#app {
    padding: 40px;
    max-width: 400px;
    position: relative;
    left: 0;
    right: 0;
    top: 50%;
    bottom: 50%;
    transform: translateY(-50%);
    background-color: white;
    margin: 0 auto;
    border-radius: 15px;
}

#app form {
    text-align: center;
}

#lblAuthState {
    text-align: center;
    max-width: 340px;
}

#btnLogout {
    width: 150px;
    margin: 0 auto;
}

#login {
    position: relative;
    top: 50%;
    bottom: 50%;
    transform: translateY(-50%);
}

.firebase-form {
    position: relative;
    max-width: 320px;
    margin: 20px auto;
    padding: 3em 2em 2em 2em;
    background: #fafafa;
    border: 1px solid #ebebeb;
    // box-shadow: rgba(0, 0, 0, 0.14902) 0px 1px 1px 0px, rgba(0, 0, 0, 0.09804) 0px 1px 2px 0px;
    z-index: 99999;
}

.group {
    position: relative;
    margin-bottom: 45px;
}

.group input {
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    -webkit-appearance: none;
    display: block;
    background: #fafafa;
    color: #636363;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #757575;
}

.group input:focus {
    outline: none;
}


/* Label */

.group label {
    color: #999;
    font-size: 18px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 10px;
    transition: all 0.2s ease;
}

/* Error Label */

.errorlabel {
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    -webkit-appearance: none;
    display: block;
    background: #fafafa;
    color: #ff0000;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #ff0000;
}


/* active */

.group input~label,
.group input.used~label {
    top: -20px;
    transform: scale(.75);
    left: -2px;
    /* font-size: 14px; */
    color: #FF6B01;
}


/* Underline */

.bar {
    position: relative;
    display: block;
    width: 100%;
}

.bar:before,
.bar:after {
    content: '';
    height: 2px;
    width: 0;
    bottom: 1px;
    position: absolute;
    background: #4a89dc;
    transition: all 0.2s ease;
}

.bar:before {
    left: 50%;
}

.bar:after {
    right: 50%;
}


/* active */

.group input:focus~.bar:before,
.group input:focus~.bar:after {
    width: 50%;
}


/* Highlight */

.highlight {
    position: absolute;
    height: 60%;
    width: 100px;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
}


/* active */

.group input:focus~.highlight {
    animation: inputHighlighter 0.3s ease;
}


/* Animations */

@keyframes inputHighlighter {
    from {
        background: #4a89dc;
    }

    to {
        width: 0;
        background: transparent;
    }
}


/* Button */

.button {
    position: relative;
    display: inline-block;
    padding: 12px 24px;
    margin: .3em 0 1em 0;
    width: 100%;
    vertical-align: middle;
    color: #fff;
    font-size: 16px;
    line-height: 20px;
    -webkit-font-smoothing: antialiased;
    text-align: center;
    letter-spacing: 1px;
    background: transparent;
    border: 0;
    // border-bottom: 2px solid black;
    cursor: pointer;
    transition: all 0.15s ease;
}

.button:focus {
    outline: 0;
}


/* Button modifiers */

.buttonBlue {
    border-radius: 5px;
    background: #FF6B01;
    // text-shadow: 1px 1px 0 rgba(39, 110, 204, .5);
}

.buttonBlue:hover {
    background: #924006;
}


/* Ripples container */

.ripples {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: transparent;
}


/* Ripples circle */

.ripplesCircle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    width: 0;
    height: 0;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.25);
}

.ripples.is-active .ripplesCircle {
    animation: ripples .4s ease-in;
}


/* Ripples animation */

@keyframes ripples {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 1;
    }

    100% {
        width: 200%;
        padding-bottom: 200%;
        opacity: 0;
    }
}

.firebase-modal {
    display: none;
    height: 100%;
    width: 100%;
}

.firebase-modal.open {
    display: block;
}

.login__close-btn, .auth__close-btn {
    position: absolute;
    right: 15px;
    top: 15px;
    border-radius: 50%;

    &:hover {
        color: #FF6B01;
    }
}

.firebase-overlay {
    display: none;
    position: fixed;
        height: 100vh;
        width: 100vw;
    
        z-index: 999999999999;
        background-color: rgba(209, 205, 205, 0.5);
}

.firebase-overlay.open {
        display: block;
        
}
